/* blocklist */
.blocklist {
  background-color: #efefef;
  box-shadow: 0px 0px 30px 5px #dfdfdf;
  /* block */ }
  .blocklist .block-container {
    position: relative;
    margin: 0.1px 0;
    padding: 0.1px 15px;
    background-color: #fff;
    /* block toolbar */
    /* block handle */
    /* props toolbar */ }
    .blocklist .block-container:hover {
      box-shadow: 0px 0px 10px 2px #dfdfdf;
      z-index: 99; }
    .blocklist .block-container.dragging {
      box-shadow: 0px 0px 15px 3px #bbb; }
    .blocklist .block-container .toolbar {
      visibility: hidden;
      position: absolute;
      top: 0px;
      padding-top: 15px;
      margin-top: -15px;
      height: calc(100% + 15px);
      margin-left: -15px;
      padding-left: 15px;
      padding-right: 15px;
      left: -55px;
      /* width:40 + padding:15 */ }
      .blocklist .block-container .toolbar.stuck-container {
        padding-right: 55px; }
      .blocklist .block-container .toolbar .toolbar-container {
        /* below are cpoied from .ui.compact.menu */
        display: inline-flex;
        margin: 0; }
      .blocklist .block-container .toolbar .handle {
        cursor: move !important; }
    .blocklist .block-container:hover .toolbar {
      visibility: visible; }
    .blocklist .block-container .handle {
      position: absolute;
      top: 0px;
      left: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 10px 0 5px;
      color: #ccc;
      cursor: all-scroll; }
    .blocklist .block-container .props-toolbar {
      visibility: hidden;
      position: absolute;
      left: 0px;
      padding-left: 15px;
      margin-left: -15px;
      padding-top: 15px;
      margin-top: -15px;
      padding-bottom: 15px;
      width: calc(100% + 15px);
      top: -55px;
      /* height:40 + padding:15 */ }
      .blocklist .block-container .props-toolbar .props-toolbar-container {
        /* below are cpoied from .ui.compact.menu */
        display: inline-flex;
        margin: 0; }
    .blocklist .block-container:hover .props-toolbar {
      visibility: visible; }
