.post.form input[name="slug"],
.post.form input[name="slug"]:focus {
  font-family: xpost, serif, 'cwTeXMing' !important; }

.post.form input[name="title"],
.post.form input[name="title"]:focus {
  background-color: inherit;
  padding: 0px;
  border: 0px;
  margin-top: 25px;
  font-size: 1.75em;
  font-family: xpost, serif, 'cwTeXMing' !important; }

.post.form input[name="subtitle"],
.post.form input[name="subtitle"]:focus {
  background-color: inherit;
  padding: 0px;
  margin-bottom: 25px;
  border: 0px;
  font-size: 1.55em;
  font-family: xpost, serif, 'cwTeXMing' !important; }

.post.form .header-wrapper {
  position: relative;
  min-height: 150px; }
  .post.form .header-wrapper .image {
    max-width: 1127px; }
  .post.form .header-wrapper .overlay {
    position: absolute;
    left: 0px;
    right: 0px; }
    .post.form .header-wrapper .overlay.top {
      top: 0px; }
    .post.form .header-wrapper .overlay.bottom {
      bottom: 0px; }
