@charset "UTF-8";
form.quote.editor, blockquote.quote.content {
  position: relative;
  margin: 10px 0;
  padding: 30px 60px 30px 80px;
  background-color: #f3f3f3; }

form.quote.editor::before, blockquote.quote.content::before {
  content: "\201C";
  display: inline-block;
  font-family: Consolas;
  position: absolute;
  top: 20px;
  left: -10px;
  font-size: 60px;
  background-color: #ccc;
  width: 60px;
  height: 60px;
  line-height: 80px;
  border-radius: 50%;
  text-align: center;
  color: white; }

form.quote.editor textarea.text, blockquote.quote.content, blockquote.quote.content span {
  overflow-wrap: break-word;
  line-height: 1.875em;
  min-height: 1.875em;
  font-size: 20px; }

form.quote.editor textarea.cite, form.quote.editor textarea.cite:focus, blockquote.quote.content cite {
  display: block;
  margin-top: 20px;
  line-height: 1.875em;
  min-height: 1.875em;
  font-size: 14px;
  color: #888;
  font-style: normal;
  text-align: right; }
  form.quote.editor textarea.cite::before, form.quote.editor textarea.cite:focus::before, blockquote.quote.content cite::before {
    content: '\FF0D';
    text-align: left;
    display: inline-block;
    width: 20px;
    font-size: 14px;
    font-family: Consolas; }

form.quote.editor textarea, form.quote.editor textarea:focus {
  border: 0px;
  padding: 0px;
  margin: 0px;
  background-color: #f3f3f3;
  outline: 1px dashed #ccc; }

blockquote.quote.content cite {
  display: block; }

blockquote.quote.content blockquote.quote.content {
  border-left: 5px solid #ccc;
  margin: 0px;
  padding: 0px 0px 0px 20px; }
  blockquote.quote.content blockquote.quote.content::before {
    display: none; }
